<template>
  <div class="lomain">
    <van-button color="#7232dd" size="large" @click="takePhotos">自定义API</van-button>
    <van-button  color="#3032dd" size="large" @click="chooseImage">拍照</van-button>
    <van-button color="#7232dd" size="large" @click="chooseLocation">地理位置</van-button>
    <van-button type="warning" size="large" @click="getRpcs">授权服务</van-button>
    <van-button size="large" @click="getParas">获取参数</van-button>
    <van-button  type="primary" size="large" @click="openS">pushwindow</van-button>
    <van-button  color="#3032dd" size="large" @click="chooseContact">调起联系人</van-button>
    <van-button color="#7232dd" size="large" @click="getNetworkType">获取网络状态</van-button>
    <van-button type="warning" size="large" @click="getBluetoothAdapterState">获取蓝牙状态</van-button>
    <van-button size="large" @click="openLocation">内置地图</van-button>
  </div>
</template>

<script>
export default {
  data(){
    return {
      longitude: '120.126293',
      latitude: '30.274653',
      name: '黄龙万科中心',
      address: '学院路77号',
    }
  },
  mounted(){
    
  },
  methods:{
    takePhotos(){
      my.call('takePhotos',(res)=>{
        my.alert({ content: JSON.stringify(res) });
      })
    },
    openLocation() {
      my.openLocation({
        longitude: this.longitude,
        latitude: this.latitude,
        name: this.name,
        address: this.address,
      })
    },
    getBluetoothAdapterState() {
      my.getBluetoothAdapterState({
        success: res => {
          if (!res.available) {
            my.alert({ content: '抱歉，您的手机蓝牙暂不可用' });
            return;
          }
          my.alert({ content: JSON.stringify(res) });
        },
        fail: error => {
          my.alert({ content: JSON.stringify(error) });
        },
      });
    },
    getNetworkType() {
      my.getNetworkType({
        success: (res) => {
          my.alert({ content: JSON.stringify(res) });
        }
      })
    },
    chooseContact() {
      my.chooseContact({
        chooseType: 'multi', // 多选模式
        includeMe: true,     // 包含自己
        includeMobileContactMode: 'known',//仅包含双向手机通讯录联系人，也即双方手机通讯录都存有对方号码的联系人
        multiChooseMax: 3,  // 最多能选择三个联系人
        multiChooseMaxTips: '超过选择的最大人数了',
        success: (res) => {
          my.alert({
            content: 'chooseContact : ' + JSON.stringify(res)
          });
        },
        fail: (res) => {
          my.alert({
            content: 'chooseContact : ' + JSON.stringify(res)
          });
        },
      });
    },
    getRpcs(){
      my.call('rpc', {
          operationType: 'com.miniprogram.test',
          requestData: [],
          headers: {
          }
      }, function(result) {
        my.alert({
          title: '您好',
          content: 'auth2服务器返回的token为'+result.resData,
          buttonText: '确定'
        });
      });
    },
    chooseLocation() {
      my.getLocation({
        success(res) {
          my.hideLoading();
          console.log(res)
          that.setData({
            hasLocation: true,
            location: formatLocation(res.longitude, res.latitude)
          })
        },
        fail(res) {
          my.hideLoading();
          my.alert({ title: res });
        },
      })
    },
    chooseImage(){
      my.chooseImage({
        sourceType: ['camera','album'],
        count: 2,
        success: (res) => {
          my.alert({
            content: JSON.stringify(res),
          });
        },
        fail:()=>{
          my.alert({
            content: 'fail', // 文字内容
          });
        }
      })
    }, 
    getParas(){
      // eslint-disable-next-line no-undef
      console.log(JSON.stringify(AlipayJSBridge.startupParams));
      // eslint-disable-next-line no-undef
      console.log(AlipayJSBridge.startupParams);
    },
    openS(){
      // eslint-disable-next-line no-undef
        AlipayJSBridge.call('pushWindow', {
          url: 'https://mcube-prod.oss-cn-hangzhou.aliyuncs.com/ONEX99480EE281121-default/20200508/0.0.0.110_all/nebula/fallback/www/index.html', // 要打开页面的 URL
          // url: 'login.html', // 要打开页面的 URL
          // 打开页面的配置参数
          param: {
            readTitle: true,    //自动读取 title
            showOptionMenu: false,    // 隐藏右边菜单
            key1: "key1Value",
            key2: "key2Value"
          },
          // 用于给新开的页面传递参数，可选
          // 在新开的页面使用 AlipayJSBridge.startupParams 可以获取到 passData
          passData: {
            key1: "key1Value",
            key2: "key2Value"
          }
        });
    }
  }
}
</script>
<style>

</style>